@import url("https://fonts.googleapis.com/css?family=Lato|Zilla+Slab");
body {
  background: #96daff;
  background: -webkit-linear-gradient(right top, #96daff 0%, #fffbe3 100%);
  background: -webkit-gradient(linear, right top, left bottom, from(#96daff), to(#fffbe3));
  background: -o-linear-gradient(right top, #96daff 0%, #fffbe3 100%);
  background: linear-gradient(to left bottom, #96daff 0%, #fffbe3 100%);
  font-family: "Lato", sans-serif; }

a {
  text-decoration: none;
  color: #395870;
  font-weight: 700; }
  a:hover {
    color: #5E6070; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40px;
  vertical-align: bottom; }

.sass-logo {
  height: 30px; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.App-header {
  padding: 2px;
  background: #7affc7;
  color: #395870;
  font-size: 24px;
  margin-bottom: 55px; }

.user {
  text-align: left;
  vertical-align: middle; }

.profile-pic {
  width: 50px;
  height: auto;
  margin-right: 12px;
  vertical-align: middle; }

.username {
  font-size: 18px; }

.sorter-heading {
  cursor: pointer; }

table {
  border-collapse: separate;
  border-spacing: 0;
  margin: 0 auto;
  margin-top: 20px;
  font: 18px/1.4 'Zilla Slab', serif; }

th,
td {
  padding: 10px 15px;
  vertical-align: middle; }

thead {
  font-family: "Lato", sans-serif;
  background: #395870;
  color: #fff; }

tbody tr:nth-child(even) {
  background: #f0f0f2; }

tbody tr:nth-child(odd) {
  background: #fff; }

td {
  border-bottom: 1px solid #cecfd5;
  border-right: 1px solid #cecfd5; }
  td:first-child {
    border-left: 1px solid #cecfd5; }

.arrow {
  color: #ccc; }

.recent {
  color: #7affc7; }

footer {
  margin-top: 25px;
  margin-bottom: 5px; }



